.Navbar {
  color: white;
  padding: 10px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.MenuIcon {
  width: 2.5vw;
  padding: 2vw;
}

.dropdown {
  position: relative;
}

.dropdown-toggle {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
}

.dropdown-menu {
  position: absolute;
  background-color: #07080d;
  top: 92.5%;
  left: -100%;
  width: 10%;
  padding: 1vw;
  transition: left 0.3s ease; 
}

.dropdown-menu ul,
.dropdown-menu li {
  list-style: none;
  font-size: 2vw;
  padding: 0;
  margin: 0;
  margin-bottom: 1vw;
}

.dropdown-menu a {
  color: aliceblue;
  text-decoration: none;
}

.Navbar.open .dropdown-menu {
  left: 0;
  margin-bottom: 10px;
}

@media (max-width:600px) {
  .MenuIcon {
    width: 10vw;
    padding: 2vw;
  }
  
  .dropdown-menu {
    width: 27%;
    padding: 3vw;
  }
  
  .dropdown-menu ul,
  .dropdown-menu li {
    font-size: 7vw;
  }
}
