#About {
  color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: rgba(218, 218, 218, 0.105);
  backdrop-filter: blur(20px);
  padding: 10vw;
  margin: 10vw;
  margin-top: 50vh;
  margin-bottom: 50vh;
}

#About h2 {
  font-size: 24px;
  font-weight: bold;
  margin: 10px 0;
}

#About h3 {
  font-size: 20px;
  font-weight: bold;
  margin: 10px 0;
}

#About p {
  font-size: 18px;
  line-height: 1.5;
  margin: 10px 0;
}

#About ul {
  list-style: none;
  padding: 0;
}

#About ul li {
  margin: 10px 0;
}

@media (max-width:600px) {
  #About {
    min-height: 100vh;
    padding: 10vw;
    margin: 0vw;
    margin-top: 50vh;
    margin-bottom: 50vh;
  }
}