.contact-form {
  width: 50%;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  color: white;
  border-radius: 10px;
}

.contact-form h2 {
  text-align: center;
}

.form-group {
  margin-bottom: 20px;
  text-align: left;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f1f1f1;
  color: #333;
  margin: 0 auto;
  text-align: left;
}

.form-group textarea {
  resize: vertical;
  height: 130px;
}

.error {
  color: #ffcc00;
}

button[type="submit"] {
  display: block;
  margin: 0 auto;
  padding: 15px;
  width: 30%;
  background-color: #928a7c;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button[type="submit"]:hover {
  background-color: #6d6455;
}

@media (max-width:600px) {
  .contact-form {
    width: 80vw;
    margin-top: 15vh;
    margin-bottom: 20vh;
  }
}
