@-webkit-keyframes tracking-in-contract {
  0% {
    letter-spacing: 1em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    letter-spacing: normal;
    opacity: 1;
  }
}
@keyframes tracking-in-contract {
  0% {
    letter-spacing: 1em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    letter-spacing: normal;
    opacity: 1;
  }
}

@media (max-width:600px) {
  .HomeLogo {
    width: 70vw;
    top: 40%;
  }
  
  .Header {
    font-size: 30vw;
    padding-top: 105vw;
  }
  
  .container {
    flex-direction: column;
    height: 150vh;
  }
  
  .section:hover {
    height: 50vh;
  }
  
}