.footer {
  background-color: #010e18cf;
  backdrop-filter: blur(20px);
  color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  margin-top: 10vw;
}

.footer-content {
  text-align: center;
  margin-top: 20px;
}

.footer-logo h2 {
  font-family: "Tangerine", cursive;
  font-size: 6vw;
  font-weight: lighter;
  margin: 3vw;
  color: #928a7c;
}

.footer-logo p {
  font-size: 2vw;
  margin: 0;
  color: #ccc;
}

.footer-nav ul {
  list-style: none;
  padding: 0;
  margin-top: 2.5vw;
}

.footer-nav li {
  margin: 10px 0;
}

.footer-nav a {
  font-weight: bold;
  margin: 2vw;
  text-decoration: none;
  color: white;
  font-size: 2vw;
  transition: color 0.3s ease;
}

.footer-nav a:hover {
  color: #928a7c;
}

.footer-info p {
  font-size: 1.5vw;
  color: #ccc;
  margin-top: 20px;
}

.footer-info p span {
  color: #42a5f5;
}

@media (max-width: 600px) {
  .footer {
    backdrop-filter: blur(10px);
    height: 50vh;
    padding: 10px;
    margin-top: 5vw;
  }
  
  .footer-content {
    margin-top: 0vh;
  }
  
  .footer-logo h2 {
    font-size: 25vw;
    margin: 0vw;
    color: #928a7c;
  }
  
  .footer-logo p {
    font-size: 2.5vw;
    margin: 0;
    color: #ccc;
  }
  
  .footer-nav ul {
    list-style: none;
    padding: 0;
    margin-top: 2.5vw;
  }
  
  .footer-nav li {
    margin: 10px 0;
  }
  
  .footer-nav a {
    font-size: 5vw;
  }
  
  .footer-info p {
    font-size: 3vw;
    color: #ccc;
    margin-top: 20px;
  }
  
  .footer-info p span {
    color: #42a5f5;
  }
  
}
