body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.header {
  background-color: #333;
  color: #fff;
  padding: 10px;
  position: fixed;
  top: 0;
  width: 100%;
}

nav ul {
  list-style: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin-right: 20px;
}

.section {
  padding: 100px 0;
  text-align: center;
}

.section:nth-child(even) {
  background-color: #f2f2f2;
}

.section h2 {
  margin: 0;
}
