#Services {
  color: white;
  font-family: Arial, sans-serif;
  text-align: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  position: relative;
}

#Services h1 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.service-container {
  background-color: #333;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 7vw;
  max-width: 50vw;
  box-sizing: border-box;
}

.container-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  width: 100%;
}

.ContainerImages {
  max-width: 45vw;
}

.service-container {
  background-color: #333;
  padding: 20px;
  border-radius: 20px;
  margin: 50px;
  max-width: 50vw;
  flex: 1;
  box-sizing: border-box;
}

.service-container h2 {
  font-size: 20px;
  font-weight: bold;
  color: #fff;
}

.service-container p {
  font-size: 18px;
  line-height: 1.5;
  color: #fff;
}

@media (max-width:600px) { 
  .ContainerImages {
    max-width: 80vw;
  }
  
  .service-container {
    max-width: 90vw;
  }
  
}
